body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

* {
    font-family: Arial, Helvetica, sans-serif;
}

:root {
    --main: #BB7C72;
    /* --main: #704944; */
    --second: #72bb9a;
    --border: #8D6962;
    --light-text: #F3EAE6;
    --dark-text: #423E39;
}
.footer {
    background: var(--dark-text);
    color: white;
    padding: 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.footer li {
    display: inline-block;
}

.footer a {
    color: white;
    text-decoration: none;
    padding: 1rem;
    margin: 1rem;
    border-radius: 5%;
}

#twitter {
    background-color: #08a0e9;
}

#facebook {
    background-color: #3B5998;
}

#instagram {
    background-color: #515BD4;
}
.Header {
    display: flex;
    justify-content: space-around;
    background-color: var(--main);
}

.navigation {
    list-style-type: none;
}

.navigation li {
    display: inline-block;
}

.navigation li a {
    display: inline-block;
    padding: 10px;
    margin: 5px;
    color: white;
    text-decoration: none;
}

.selected {
    background-color: var(--second);
    border-radius: 10px;
}

h1 a {
    color: white;
    text-decoration: none;
    margin: 1rem;
}

.home section {
    background-image: url('https://images.unsplash.com/photo-1522276498395-f4f68f7f8454?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3298&q=80');
    max-width: 100%;
    height: auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.home section>div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: rgba(66, 62, 57, 0.5);
    height: 700px;
}

.description {
    display: inline-block;
    width: 500px;
}

.shelterForm {
    margin-top: 50px;
    background-color: var(--light-text);
    padding: 5rem;
    border-bottom: 10px solid var(--second);
    display: flex;
    flex-direction: column;
}

.shelterForm h3 {
    color: var(--dark-text);
    font-size: 45px;
}

.shelterForm div {
    display: flex;
    justify-content: center;
    margin: 1rem;
}

.shelterForm input {
    border: none;
    background: white;
    padding: 0.5rem;
    width: 200px;
    border-left: 5px solid var(--border);
}

.shelterForm label {
    color: var(--dark-text);
    font-size: 2rem;
    font-weight: bold;
    padding-right: 0.75rem;
}

.shelterForm button {
    padding: 1rem;
    margin: 0 1rem;
    font-size: 1rem;
    border: 2px solid var(--light-text);
    background: var(--border);
    color: var(--light-text);
}

.shelterForm button:hover {
    border: 2px solid var(--border);
    background: var(--light-text);
    color: var(--border);
    cursor: pointer;
}

.info {
    text-align: left;
    color: white;
}

.info h2 {
    font-size: 50px;
}

.info p {
    font-size: 24px;
}
.resShelter {
    margin-left: 300px;
    margin-right: 300px;
}

.shelterWrap {
    display: flex;
    flex-direction: column;
}

.shelters {
    justify-content: left;
    display: flex;
    margin: 1rem;
    align-items: center;
}

.shelters h4 {
    margin-right: 10px;
}

.shelters button {
    padding: 0.25rem 0.5rem;
    margin: 0 1rem;
    font-size: 1rem;
    border: 2px solid var(--light-text);
    background: var(--border);
    color: var(--light-text);
}

.shelterPets {
    justify-content: space-around;
    margin-left: 15px;
    display: flex;
    
}

.petName {
    /* display: flex; */
    /* flex-direction: column; */
}

/* Pagination */
.pagination li {
    display: inline-block;
    margin: 1rem;
}

.pagination button {
    background: var(--light-text);
    padding: 1rem;
    cursor: pointer;
}

.pagination li:first-child button {
    border: none;
    border-left: 5px solid var(--second);
}

.pagination li:last-child button {
    border: none;
    border-right: 5px solid var(--second);
}

button#begin {
    border-left: 5px solid var(--main);
    pointer-events: none;
}

button#end {
    border-right: 5px solid var(--main);
    pointer-events: none;
}

/* modal */

.peticon {
    width: 95px;
    height: 95px;
    overflow: hidden;
}

.petDisplay {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.shelterDisplay {
    background-color: var(--light-text);
    margin: 0;
    padding: 2rem;
    border-bottom: 5px solid var(--border);
    margin-bottom: 1rem;
}

.modalImg {
    margin-left: 20px;
    max-height: 500px;
}

.modalText {
    max-width: 500px;
}

.petLists div{
    width: 300px;
    height: 300px;
    overflow: hidden;
}

.pagination button {
    background: var(--light-text);
    padding: 1rem;
    cursor: pointer;
}

.pagination li:first-child button {
    border: none;
    border-left: 5px solid var(--second);
}

.pagination li:last-child button {
    border: none;
    border-right: 5px solid var(--second);
}

button#begin {
    border-left: 5px solid var(--main);
    pointer-events: none;
}

button#end {
    border-right: 5px solid var(--main);
    pointer-events: none;
}

